.scanning-section {
  padding-top: 0;
  padding-bottom: 40px;
}
.scanning-container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}
.scanning-section__title {
  margin-top: 0;
  margin-bottom: 16px;
  font-family: 'CeraPro';
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.25;
  color: #487996;
  text-transform: uppercase;
}
.scan-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.scan-list__item {
  width: 130px;
  height: 182px;
  margin-right: 20px;
  margin-bottom: 16px;
  text-align: center;

  // display: block;
  // align-items: center;
  // justify-items: center;

  &:nth-child(2n) {
    margin-right: 0;
  }

  &:nth-last-child(-n + 2) {
    margin-bottom: 0;
  }
}

.scan-list__canvas {
  position: relative;
  width: 100%;
  height: 100%;
  background: #ffffff;
  opacity: 0.8;
  box-shadow: inset 1px 1px 16px rgba(203, 203, 203, 0.8),
    inset -1px -1px 16px #ffffff;
  border-radius: 50px 0px;

  &::after {
    content: ' ';
    position: absolute;

    left: 15px;
    top: 15px;

    background-image: url('/src/images/3d-scanning/mobile/scan-process-1.png');
  }
}
.scan-list__title {
  position: absolute;
  top: 114px;
  left: 0;
  text-align: center;

  font-family: 'Cera Pro';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.28;
  color: #487996;
}
